const markdownText = `
Welcome to our Terms of Service page. These terms outline the rules and regulations for the use of our website and services. By accessing or using our website or services, you agree to comply with these terms. If you do not agree with any part of these terms, please do not use our website or services.

---

## Use of Our Website and Services

- You are responsible for maintaining the confidentiality of your account information and are solely responsible for all activities that occur under your account.
- You agree not to use our website and services for any illegal or unauthorized purpose.
- We reserve the right to terminate or suspend your access to our website and services at any time for any reason without prior notice.

## Intellectual Property Rights

- The content, trademarks, logos, and intellectual property displayed on our website are owned by us or our licensors. You may not use, reproduce, or distribute any of the content without our prior written permission.

## Limitation of Liability

- We strive to provide accurate and up-to-date information on our website, but we do not guarantee the accuracy, completeness, or reliability of the content. You use our website and services at your own risk.
- We are not liable for any direct, indirect, incidental, consequential, or special damages that may arise from the use of our website or services.
- We are not responsible for any third-party websites or services that may be linked to or from our website.

## Indemnification

- You agree to indemnify and hold us harmless from any claims, damages, liabilities, and expenses arising out of your use of our website and services or your violation of these terms.

## Governing Law and Jurisdiction

- These terms are governed by and construed in accordance with the laws of [Your Jurisdiction]. Any disputes relating to these terms shall be subject to the exclusive jurisdiction of the courts in [Your Jurisdiction].

## Changes to these Terms

- We reserve the right to update or modify these terms at any time without prior notice. Any changes will be effective immediately upon posting the updated terms on our website. You are responsible for regularly reviewing these terms to stay informed about any changes.

---

If you have any questions or concerns regarding these terms, please contact us using the information provided below:

- Email: [contact.infolsco@gmail.com](mailto:contact.infolsco@gmail.com)
- Twitch (DM): [twitch.tv/LsCo](https://twitch.tv/LsCo)
- Twitter (DM): [twitter.com/@devLsCo](https://twitter.com/@devLsCo)
`;

const Page = () => {
  useEffect(() => {
    document.title = 'LsCo Software - Privacy Policy';
  }, []);

  return (
    <MainCard title="Privacy Policy" description="Last Updated: August 4, 2023">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12}>
          <Typography
            component={ReactMarkdown}
            variant="body1"
            sx={{
              fontSize: 16,
              '& a': {
                color: 'gray'
              }
            }}
          >
            {markdownText}
          </Typography>
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
